/* Typography*/
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500&family=Merriweather:wght@900&display=swap");
/* alerts  onloading animations */
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

html {
  font-size: 100%;
}

/*16px*/
body {
  width: 100vw;
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: white;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 400;
  line-height: 1.75;
  color: #0f172a;
}

p {
  max-width: 40em;
  margin-bottom: 1.5rem;
}

h1,
h2 {
  margin: 0;
  margin-bottom: 1.38rem;
  font-family: "Merriweather", serif;
  font-weight: 400;
  text-transform: capitalize;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

a {
  text-decoration: none;
  color: #1944dd;
}

/* buttons */
.btn {
  cursor: pointer;
  color: white;
  background: #2852e7;
  border: transparent;
  border-radius: 28px;
  letter-spacing: 1px;
  padding: 0.75rem 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: 0.3s ease-in-out all;
  text-transform: initial;
  display: inline-block;
  font-size: 1rem;
  /*block class is good with forms*/
}
.btn:hover {
  background: #0c216a;
  transform: scale(0.98);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.btn-secondary {
  color: #4e546a;
  background: #f1f4ff;
  border: 1px solid #d7e0ff;
}
.btn-secondary:hover {
  color: #f1f4ff;
  background: #4e546a;
}

/* alerts */

/* forms */

/* titles */
.title {
  text-align: center;
}
.title h1 {
  font-size: 28px;
  margin: 0;
  color: #f1f4ff;
}

.title-underline {
  background: #f1f4ff;
  width: 7rem;
  height: 0.25rem;
  margin: 0 auto;
  margin-top: 1rem;
}

/* images */

.card {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
  padding: 1.25rem 2rem;
  border-radius: 25px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  background-color: #e2e8f0;
  z-index: 9;
  overflow: hidden;
  position: relative;
}
.card.card-subscribe {
  width: 100%;
  max-width: 570px;
}
.card .card-body {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.card .card-body .bell-wrapper {
  margin-top: 2rem;
  padding: 1rem;
  border-radius: 1.25rem;
  background-color: #f1f4ff;
  display: grid;
  place-items: center;
}
.card .card-body #bell {
  color: #645cff;
}
.card .card-body #close,
.card .card-body #close2 {
  color: #94a3b8;
  align-self: flex-start;
  position: absolute;
  right: 15px;
}
.card .card-body #google {
  color: #2852e7;
}
.card.card-join {
  width: 100%;
  max-width: 450px;
}
.card.card-join .buttons {
  background-color: #2852e7;
  border-radius: 28px;
  padding: 0.1rem 0.2rem;
  cursor: pointer;
  transition: 0.3s ease-in-out all;
}
.card.card-join .buttons span {
  padding: 0.75rem;
  border-radius: 50%;
  background-color: #fff;
  display: grid;
  place-items: center;
}
.card.card-join .buttons p {
  font-size: 14px;
  padding-right: 0.5rem;
  color: #fff;
}
.card.card-join .buttons:hover {
  background: #0c216a;
  transform: scale(0.98);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.card .card-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 0 2rem;
  padding-top: 2rem;
}
.card .card-text h2 {
  font-size: 16px;
  margin: 0;
  color: #19163c;
  text-transform: initial;
}
.card .card-text p {
  font-size: 12px;
  font-weight: 400;
  color: #19163c;
  max-width: 200px;
  margin: 0;
}
.card .buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.card .card-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (min-width: 768px) {
  .card {
    padding: 30px 40px;
  }
  .card .card-body .bell-wrapper {
    width: 100px;
    height: 100px;
    margin-left: -2rem;
    padding: 0;
  }
  .card .card-text h2 {
    font-size: 22px;
  }
  .card .card-text p {
    font-size: 1rem;
    max-width: 300px;
  }
}
@media (min-width: 1024px) {
  .card.card-subscribe {
    max-height: 270px;
    transform: translateX(-50%);
  }
  .card.card-join {
    max-height: 340px;
    transform: translateX(50%);
  }
}
.container {
  width: 100%;
  max-width: 1440px;
  min-height: 100vh;
  padding: 0 20px;
  display: grid;
  place-items: center;
  gap: 1rem;
  position: relative;
  background-color: #4e546a;
}
.container::after {
  content: "";
  width: 213px;
  height: 452px;
  position: absolute;
  background-color: #414863;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

*,
::after,
::before {
  box-sizing: border-box;
}